import React from "react";
import { Row, Col } from "react-bootstrap";
import avatar from "../../images/avatar.png";
import style from "./AdminAddCategory.module.css";

const AdminAddCategory = () => {
  return (
    <div>
      <Row className="justify-content-start ">
        <div className={`${style["admin-content-text"]} pb-4`}> </div>
        <Col sm="8">
          <div className={`${style["text-form"]} pb-2`}> </div>
          <img src={avatar} alt="" height="100px" width="120px" />
          <input type="text" className={`${style["input-form"]} d-block mt-3 px-3`} placeholder=" " />
        </Col>
      </Row>
      <Row>
        <Col sm="8" className="d-flex justify-content-end ">
          <button className={`${style["btn-save"]} d-inline mt-2`}> </button>
        </Col>
      </Row>
    </div>
  );
};

export default AdminAddCategory;
